.NavbarItems {
  background: linear-gradient(90deg, rgb(17, 2, 129), rgba(73, 63, 252, 0.9));
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.navbar-logo {
  color: white;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  font-family: rubik;
}
.fa-react {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}
.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 20px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
  font-family: rubik;
  padding: 2px 10px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem, 1rem;
}

.nav-links:hover {
  background-color: blueviolet;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}

.fa-bars {
  color: white;
}

.nav-links-mobile {
  display: none;
}
.menu-icon {
  display: none;
}

.sticky {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.6);
  position: sticky;
  top: 0;
  z-index: 100;
}
.Caps {
  font-size: 3rem;
}

@media screen and (max-width: 680px) {
  .NavbarItems {
    position: relative;
  }
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .nav-menu.active {
    background: rgba(26, 4, 107, 0.315);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #7577fa;
    border-radius: 0;
  }
  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(20%, 20%);
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .fa-times {
    color: white;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: rgb(216, 4, 121);
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
  }
  .nav-links-mobile:hover {
    background-color: white;
    color: blue;
    transition: 250ms;
  }
}
