.Main {
  display: grid;
  grid-template-columns: 20% 80%;
  margin: 20px 100px;
  padding: 10px;
  width: 80vw;
  max-width: 90vw;
  height: 400px;
  position: absolute;
  border-radius: 15px;
  box-shadow: 5px 5px #88888836;
}

.MainSub {
  display: flex;
  flex-direction: column;

  justify-content: space-around;
  background-color: rgb(4, 3, 88);
  padding: 10px 0 10px 10px;
  color: white;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  font-family: oswald;
  text-align: center;
  font-size: 1.6rem;
}
.MainSubb {
  background-color: rgb(229, 232, 243);
}
.iconSize {
  font-size: 10rem;
  padding: 10px;
}

.Resumett {
  font-size: 1.5rem;
  font-family: aleo;
  padding: 10px;
  opacity: 0.8;
}

@media screen and (max-width: 790px) {
  :root {
    font-size: 11px;
  }
  .Main {
    margin: 20px 50px;
    height: 350px;
  }
}
