.HeroCom {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 85vh;
  background-image: linear-gradient(
      146deg,
      rgba(110, 94, 254, 1) 0%,
      rgba(132, 221, 250, 0.7334908963585435) 55%,
      rgba(138, 254, 249, 0.1) 72%
    ),
    url("https://images.unsplash.com/photo-1451187580459-43490279c0fa?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1172&q=80");
  background-position: center;
  background-size: cover;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
  margin: 0 0px;
  padding: 0 50px;
}

.MainTextHero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
}
.myName {
  font-size: 2rem;
  font-family: poppins;
  padding: 3px;
}
.HeroInfo {
  font-size: 3.5rem;
  font-family: oswald;
  font-weight: 700;
  padding: 3px;
  color: white;
}
.AboutJob {
  font-size: 1.5rem;
  font-weight: 400;
  font-family: poppins;
  padding: 3px;
}
.name-Im-text {
  font-size: 4rem;
  font-family: oswald;
  padding: 3px;
}

.ContactHero {
  font-size: 1.6rem;
  font-family: ubuntu;
  width: 150px;
  padding: 4px;
  border-radius: 15px;
  margin: 0 30px;
  background-color: rgba(207, 142, 2, 0.582);
  color: rgb(248, 244, 244);
  border: none;
  box-shadow: 2px;
}
.MainPicture {
  padding: 15px;
}

@media screen and (max-width: 680px) {
  :root {
    font-size: 11px;
  }
}
