.Aboutme {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.AboutTitle {
  font-size: 3.5rem;
  font-family: oswald;
  font-weight: 700;
  padding: 3px;
  color: rgb(66, 62, 62);
}
.whyMe {
  font-size: 1.5rem;
  font-weight: 200;
  font-family: poppins;
  padding: 8px;
  opacity: 0.6;
  padding-bottom: 30px;
}
.MainBox {
  display: flex;
  padding: 15px;
  justify-content: space-between;
  box-shadow: 0 4px 15px rgb(0 0 0 / 0.3);
  border-radius: 15px;
  width: 85%;
  margin: 0 15px;
}

.HighlightsRight {
  font-size: 1.5rem;
  font-weight: 200;
  font-family: aleo;
  padding: 3px;
  opacity: 0.6;
}

.HighlightsUl {
  font-size: 0.8rem;
  font-weight: 200;
  font-family: sans-serif;
  padding: 3px;
  opacity: 1;
  color: black;
}
@media screen and (max-width: 590px) {
  .MainBox {
    display: flex;
    flex-direction: column;
    padding: 15px;
    justify-content: space-between;
    box-shadow: 0 4px 15px rgb(0 0 0 / 0.3);
    border-radius: 15px;
    width: 85%;
    margin: 0 15px;
  }
}
