.MainContact {
  display: flex;
  flex-direction: column;
  justify-content: safe;
  align-items: center;
}

.Contact-Text {
  font-size: 3rem;
  font-family: oswald;
  opacity: 0.8;
  font-weight: 600;
  padding: 15px;
}

.Maininfo {
  display: flex;
  justify-content: space-around;

  width: 80vw;
  background: linear-gradient(90deg, rgb(21, 4, 153), rgba(110, 103, 243, 0.9));
}
.phone {
  background-color: rgba(199, 173, 173, 0.192);
  border-radius: 15px;
  padding: 20px;
  height: 150px;
  margin-top: 30px;
  color: white;
}

.email {
  background-color: rgba(199, 173, 173, 0.192);
  border-radius: 15px;
  padding: 20px;
  height: 150px;
  margin-top: 30px;
  color: white;
}

@media screen and (max-width: 710px) {
  .Maininfo {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    width: 80vw;
    background: linear-gradient(
      90deg,
      rgb(21, 4, 153),
      rgba(110, 103, 243, 0.9)
    );
  }
}
